.contacts__text {
  font-size: 18px;

  margin: 0 0 10px;
}

.contacts__email {
  color: #000;
  font-size: 18px;
  text-align: center;
  

  margin: 0 0 15px;

  a {
    color: #000;
    font-weight: 700;
  
    position: relative;
    padding: 0 0 5px;
    transition: all 0.2s ease-in-out;
  
    &:hover,
    &:focus {
      color: var(--second-color);
  
      &::after {
        width: 100%;
      }
    }
  
    &::after {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      height: 2px;
      width: 0;
      background-color: var(--second-color);
      transition: all 0.2s ease-in-out;
    }
  }
}

.contasts__form {
  p {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 0 0 15px;
  }
}

.contacts__label {
  font-size: 16px;
  line-height: 20px;

  margin: 0 0 3px;

  sup {
    color: #f11a48;
  }
}

.contacts__field {
  font-size: 18px;
  font-weight: 400;
  font-family: var(--main-font);

  width: 100%;
  height: 45px;
  padding: 10px 5px;
  border: none;
  border-bottom: 2px solid #333;

  &:hover,
  &:focus {
    border-color: var(--second-color);
  }

  &--area {
    height: 70px;
    resize: vertical;
  }
}

.text-filter {
  width: 100%;
  font-size: 3rem;
  fill: var(--second-color);
}

@media (min-width: 767px) {
  .text-filter {
    font-size: 5rem;
  }

  .contacts__email {
    font-size: 22px;
  }
}