.about {
  .social__list {
    justify-content: flex-start;
  }
}

.about__pic {
  text-align: center;

  position: relative;
  margin: 0 0 15px;

  img {
    border-radius: 5px;
  }
}

.about__text {
  font-size: 18px;
  font-weight: 400;

  margin: 0 0 10px;

  b {
    //color: var(--second-color);
  }
}

@media (min-width: 767px) {
  .about {
    display: flex;
    justify-content: space-between;
    align-items: top;
  }

  .about__pic {
    min-width: 40%;
    margin-right: 30px;
  }
}

@media (min-width: 1000px) {
  .about__pic {
    min-width: 40%;
    margin-right: 40px;
  }

  .about__text {
    font-size: 22px;
  }
}