.projects {
  .button__wrapper {
    justify-content: space-between;
  }
  .button {
    width: 49%;
  }
}

.projects__title {
  font-size: 20px;
}

.projects__list {
  @include reset-list();
}

.projects__item {
  position: relative;
  max-width: 420px;
  margin: 0 auto 20px;
  padding: 0 0 20px;
  border-bottom: 1px solid var(--main-color);

  &:last-child {
    margin-bottom: 0;
    border: none;
  }
}

.projects__stack {
  p {
    font-size: 16px;

    margin: 0 0 10px;
  }

  ul {
    @include reset-list();
    margin: 0 0 10px;
  
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }
  li {
    font-size: 18px;
    color: var(--main-color);

    display: block;
    padding: 4px 10px;
    margin: 0 8px 8px 0;
    background-color: #fff;
    border: 2px solid var(--main-color);
  }
}

.projects__pic {
  background-color: #eee; 
  border: 7px solid #000;
  border-radius: 4px;
  overflow-y: auto;

  img {
    width: 100%;
  }

  &--desktop {
    position: relative;
    width: 100%;
    height: 200px;
    margin: 0 auto;
  }
}

.projects__view--mobile {
  display: none;
}

@media (min-width: 410px) {
  .projects__pic--desktop {
    height: 250px;
  }
}

@media (min-width: 767px) {
  .projects__item {
    max-width: 600px;
    margin: 0 auto 60px;
    padding: 0 0 30px;
  }

  .projects__view {
    position: relative;

    &:hover {
      &::before {
        content: "";
        position: absolute;
        bottom: 3px;
        left: 50%;
        transform: translateX(-50%);
        height: 6px;
        width: 40%;
        border-radius: 5px;
        background-color: var(--second-color);
        z-index: 1;
      }
    }

    &--mobile {
      position: absolute;
      top: -30px;
      left: -20px;
      display: block;
      width: 250px;
      z-index: 4;
    }

    &--current {
      z-index: 5;
    }
  }

  .projects__pic {
    border-width: 12px;
    height: 350px;
  }

  .projects__stack p {
    font-size: 18px;
  }
}

@media (min-width: 1200px) {
  .projects__list {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-top: 50px;
  }

  .projects__item {
    width: 47%;
    margin: 0 0 80px;
    border: none;

    &:nth-last-child(2) {
      margin: 0;
    }
  }
}