.popup {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 80%;
  max-height: 90vh;
  z-index: 100;
  padding: 25px;
  background: linear-gradient(145deg, #fff, #fff 65%, #eee 0);
  box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.5);
}

.popup__close {
  position: absolute;
  top: 5px;
  right: 5px;
  border: none;
  background-color: transparent;
  padding: 0;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: #eee;
  cursor: pointer;

  &::after,
  &::before {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    width: 70%;
    height: 2px;
    background-color: #000;
  }

  &::after {
    transform: translate(-50%, -50%) rotate(45deg);
  }

  &::before {
    transform: translate(-50%, -50%) rotate(-45deg);
  }
}

@media (min-width: 767px) {
  .popup {
    width: 400px;
    padding: 30px;
  }

  .popup__close {
    width: 25px;
    height: 25px;
  }
}

@media (min-width: 1000px) {
  .popup {
    width: 450px;
  }
}