.header {
  position: relative;
  padding: 20px 0;
  background-color: var(--main-color);
  background-repeat: no-repeat;
  background-position: 50%;
  background-size: cover;
  background-image: url("./../img/main-bg-2.jpg");

  // &::before {
  //   content: "";
  //   position: absolute;
  //   top: 0;
  //   left: 0;
  //   bottom: 0;
  //   right: 0;
  //   background-color: rgba(0, 0, 0, 0.5);
  // }
}

@media (min-resolution: 1.5dppx) {
  .header {
    background-image: url("./../img/main-bg-2@2x.jpg");
  }
}

.header__wrapper-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__title {
  font-size: 40px;
  font-weight: normal;
  text-transform: uppercase;
  color: #000;
  text-align: center;

  padding: 110px 20px 160px;
  margin: 0;

  span {
    font-size: 16px;
    text-transform: none;

    position: relative;
    display: block;
    margin: 20px 0 0;
    padding: 20px 0 0;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
      width: 150px;
      height: 1px;
      background-color: #000;
    }
  }
}

@media (min-width: 767px) {
  .header {
    padding: 25px 0;
  }

  .header__title {
    font-size: 45px;
    padding: 110px 20px 200px 20px;

    span {
      font-size: 20px;
    }
  }
}

@media (min-width: 1000px) {
  .header__logo {
    width: 60px;
    height: 60px;

    img {
      width: 60px;
      height: 60px;
    }
  }

  .header__title {
    font-size: 50px;

    span {
      font-size: 25px;
    }
  }
}

@media (min-width: 1200px) {
  .header {
    background-position: 0 75%;
  }

  .header__title {
    padding: 200px 350px 200px 20px;
  }
}