.education {
  .button {
    text-transform: none;
    background-color: transparent;
    border-color: #4eb543;

    &:hover,
    &:focus {
      color: #fff;
      background-color: #4eb543;
    }
  }
}

.education__list {
  @include reset-list();
}

.education__item {
  text-align: center;
  
  padding: 20px 30px;
  margin: 0 0 30px;
  background-color: #302683;
  background-image: repeating-linear-gradient(150deg,rgba(255,255,255,.06),rgba(255,255,255,.06) 1px,transparent 0,transparent 41px),repeating-linear-gradient(-150deg,rgba(255,255,255,.06),rgba(255,255,255,.06) 1px,transparent 0,transparent 41px); 

  &--js {
    background-color: #18213f;
  }

  &:last-child {
    margin: 0;
  }
}

.education__title {
  font-weight: 700;
  font-size: 17px;
  color: #fff;

  margin: 0 0 15px;
}

.education__link {
  color: #fff;
  transition: text-decoration 0.2s ease;

  &:hover {
    text-decoration: underline;
  }
}

@media (min-width: 767px) {
  .education__list {
    width: 80%;
    margin: 0 auto;
  }

  .education__item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 30px;
    margin: 0 0 40px;
  }

  .education__title {
    text-align: left;
    margin: 0;
  }
}

@media (min-width: 1000px) {
  .education__item {
    padding: 40px;
  }

  .education__title {
    font-size: 20px;
  }
}

@media (min-width: 1200px) {
  .education__item {
    padding: 50px;
  }

  .education__title {
    font-size: 24px;
  }
}