.button__wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.button {
  font-size: 18px;
  color: #fff;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;

  position: relative;
  display: inline-block;
  border: 2px solid var(--second-color);
  border-radius: 0;
  padding: 10px;
  background-color: var(--second-color);
  transition: all 0.2s ease;
  cursor: pointer;

  &:hover,
  &:focus {
    color: var(--second-color);
    background-color: #fff;
  }
}