:root {
  --main-font: "Roboto", "Arial", sans-serif;
  --main-color: #344955;
  --second-color: darksalmon;
  --hover-color: #a3ddfa;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-size: 14px;
  line-height: normal;
  color: #000;
  font-family: var(--main-font);

  background-color: #fff;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  min-width: 320px;
  margin: 0;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;

  @media (min-width: 767px) {
    padding: 0 30px;
  }

  @media (min-width: 1000px) {
    padding: 0 40px;
  }

  @media (min-width: 1200px) {
    width: 1150px;
  }
}

@mixin reset-list {
  list-style: none;
  margin: 0;
  padding: 0;
  border: none;
}
