.menu-toggle {
  position: relative;
  width: 40px;
  height: 40px;
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
  z-index: 30;
  border-radius: 50%;

  span {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: block;
    width: 24px;
    height: 3px;
    background-color: #000;

    &::before,
    &::after {
      content: "";
      position: absolute;
      left: 0;
      width: 100%;
      height: 3px;
      background-color: #000;
      transform-origin: 50% 50%;
      transition: top 0.3s 0.3s, transform 0.3s;
    }

    &::after {
      top: -7px;
    }

    &::before {
      top: 7px;
    }
  }
}

.menu-toggle--opened {
  position: fixed;
  top: 22px;
  right: 20px;
  background-color: #eee;

  span {
    background-color: transparent;
    &::after {
      transform: rotate(45deg);
    }

    &::before {
      transform: rotate(-45deg);
    }

    &::before,
    &::after {
      top: 0;
      transition: top 0.3s, transform 0.3s  0.3s;
    }
  }
}

@media (min-width: 767px) {
  .menu-toggle {
    display: none;
  }
}