.footer {
  width: 100%;
  background-color: var(--main-color);
  padding: 10px 0;
}

.footer__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.footer .social__icon {
  fill: #fff;
}

@media (min-width: 767px) {
  .footer__wrapper {
    flex-direction: row;
    justify-content: space-between;
  }
}

@media (min-width: 1000px) {
  .footer {
    padding: 20px 0;
  }
}

@media (min-width: 1200px) {
  .footer {
    padding: 25px 0;
  }
}