.menu__wrapper {
  display: none;
  flex-direction: column;
  align-items: center;
  position: fixed;
  top: 8px;
  bottom: 8px;
  left: 8px;
  right: 8px;
  background: linear-gradient(145deg, #fff, #fff 65%, #eee 0);
  transition: all 0.2s ease-in-out;
  z-index: 20;
  padding: 0 20px 20px;
  box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.5);
}

.menu {
  margin: auto 0;
}

.menu__list {
  @include reset-list();

  display: flex;
  flex-direction: column;
  justify-content: center;
}

.menu__link {
  font-size: 18px;
  text-transform: uppercase;
  color: #272826;
  text-align: center;

  position: relative;
  display: block;
  padding: 15px 10px;
  transition: all 0.2s ease-in-out;
  z-index: 5;
}

.menu--opened {
  display: flex;
}

@media (min-width: 767px) {
  .menu__wrapper {
    display: flex;
    flex-direction: row;
    position: static;
    background: none;
    padding: 0;
    box-shadow: none;
  }

  .menu__wrapper .social {
    display: none;
  }

  .menu__list  {
    flex-direction: row;
  }

  .menu__item  {
    padding: 0 10px;
  }

  .menu__link {
    font-size: 14px;
    position: relative;
    padding: 10px 0;

    &::before {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 2px;
      background: #272826;
      opacity: 0;
      transition: all .3s;
      transform: translateY(10px);
    }

    &:hover::before,
    &:focus::before {
      opacity: 1;
      transform: translateY(0px);
    }
  }
}

@media (min-width: 1000px) {
  .menu__link {
    font-size: 16px;
  }
}
