.social__list {
  @include reset-list();
  
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}

.social__item {
  margin: 5px;
}

.social__link {
  display: block;
  width: 20px;
  height: 20px;

  svg {
    transition: all 0.2s ease-in-out;
  }

  &--email {
    &:hover,
    &:focus {
      svg {
        fill: #fbd00a;
      }
    }
  }
  &--github {
    &:hover,
    &:focus {
      svg {
        fill: #8c43ff;
      }
    }
  }
  &--telegram {
    &:hover,
    &:focus {
      svg {
        fill: #2ca5e0;
      }
    }
  }
  &--vk {
    &:hover,
    &:focus {
      svg {
        fill: #6089c0;
      }
    }
  }
  &--codepen {
    &:hover,
    &:focus {
      svg {
        fill: #f97058;
      }
    }
  }
  &--instagram {
    &:hover,
    &:focus {
      svg {
        fill: #d23287;
      }
    }
  }
}
